import { Column, Container, Row } from "components/layout"
import styled from "styled-components"
import { SharedImage } from "constants/image-constants"
import Image from "components/image";
import { HexadoCard } from "components/card";
import { Splitter } from "components/border";
import CheckPoint from "components/checkpoint";
import { BoxRadientLeft, BoxRadientRight, DevBox } from "components/box";
import { Button } from "components/button";

const { HEXADO_PROJECT, HEXADO_NFT, HEXADO_LOGO_2, HEXADO_TYPES, HEXADO_HADES, HEXADO_HADES_LETTER, HEXADO_VISHNU, HEXADO_DEVIL, HEXADO_CLEPTO, HEXADO_VISHNU_LETTER, HEXADO_DEVIL_LETTER, HEXADO_CLEPTO_LETTER, HEXADO_PROJECT_2, HEXADO_FUTURE_LOGO, HEXADO_GENERATIVE_DROP, HEXADO_DROPS, HEXADO_VXAIATARS, HEXAVERSE, HEXADO_STORE, HEXADO_CLOTHING, HEXADO_AIDEN, HEXADO_COLLIN, HEXADO_FITZ, HEXADO_MAIN, HEXADO_TEAM, HEXADO_COLIN_LETTER, HEXADO_COLIN_ROLE, HEXADO_AIDEN_LETTER, HEXADO_AIDEN_ROLE, HEXADO_FITZ_LETTER, HEXADO_FITZ_ROLE, HEXADO_MAIN_LETTER, HEXADO_MAIN_ROLE, HEXADO_JV, HEXADO_JV_LETTER, HEXADO_JV_ROLE, HEXADO_KARL_ROLE, HEXADO_KARL, HEXADO_KARL_LETTER, HEXADO_NGU, HEXADO_NGU_LETTER, HEXADO_NGU_ROLE } = SharedImage;

export default function LandingPage() {

    return (
        <Wrapper>
            <Container>
                <Row className="hexado-hero" justifyContent="space-between">
                    <Column className="hexado-info">
                        <Image src={HEXADO_PROJECT} />
                        <p className="hexado-about">
                            Hexado‚Äôs Project is a collection comparable to no other. A product of expression resonating from a background of graffiti and other underground niches. A creation deeper than NFT‚Äôs and a future experimenting with early metaverse innovations.
                        </p>
                        <Button className="hexados-hero-btn">
                            Learn more
                        </Button>
                    </Column>
                    <Image src={HEXADO_NFT} className="hexado-nft" />
                </Row>
                <Row className="hexado-influence">
                    <Image src={HEXADO_LOGO_2} />
                    <Image src={HEXADO_LOGO_2} />
                    <Image src={HEXADO_LOGO_2} />
                </Row>
                <Column alignItems="center">
                    <Image src={HEXADO_TYPES} dataAos="fade-up" />
                    <HexadoList>
                        <HexadoCard data-aos="fade-up">
                            <Image src={HEXADO_HADES} />
                            <Image src={HEXADO_HADES_LETTER} className="hexado-card-letter" />
                            <Splitter />
                            <p>
                                The Greek god of the dead and the ruler of the underworld. Hades is also associated with great riches. The god ‚ÄúHades‚Äù impersonates the first drop perfectly. He is the ruler of wealth, which all of my initial investors will be able to associate with.
                            </p>
                        </HexadoCard>
                        <HexadoCard data-aos="fade-up" data-aos-delay="300">
                            <Image src={HEXADO_VISHNU} />
                            <Image src={HEXADO_VISHNU_LETTER} className="hexado-card-letter" />
                            <Splitter />
                            <p>
                                Vishnu is the Supreme God in Hinduism. The Vishnu Sahasranama declares Vishnu as Paramatman (supreme soul) and Parameshwara (supreme God) The god ‚ÄúVishnu‚Äù fits the drop very well. The word "supreme" is certainly how Hexado‚Äôs Project investors are gonna feel after they don't get grey-area-rugged.
                            </p>
                        </HexadoCard>
                        <HexadoCard data-aos="fade-up" data-aos-delay="600">
                            <Image src={HEXADO_DEVIL} />
                            <Image src={HEXADO_DEVIL_LETTER} className="hexado-card-letter" />
                            <Splitter />
                            <p>
                                Devil is associated with pain and evil. One can see how happy/sad/mischievous each NFT looks. A few Devil/Demons were seen due to the amount of sleep deprivation endured in the continued creation of this project.
                            </p>
                        </HexadoCard>
                        <HexadoCard data-aos="fade-up" data-aos-delay="900">
                            <Image src={HEXADO_CLEPTO} />
                            <Image src={HEXADO_CLEPTO_LETTER} className="hexado-card-letter" />
                            <Splitter />
                            <p>
                                ‚ÄúClepto‚Äù comes from the Latin word ‚ÄúKlepto‚Äù. This means ‚Äúthief‚Äù in Latin. This is relevant because so many of you guys seem to invest into these goofy-ass projects and lose the money you could have spent on your kids or girlfriend.
                            </p>
                        </HexadoCard>
                    </HexadoList>
                </Column>
                <Row justifyContent="center" className="hexado-project-2">
                    <Image src={HEXADO_PROJECT_2} />
                </Row>
                <Column alignItems="center" className="hexado-future">
                    <Image src={HEXADO_FUTURE_LOGO} />
                    <p>
                        As of right now, Hexados project is extremely early when it comes to long term innovations. Our status right now is a little fish in a sea of sharks. That doesnt change the fact we are still one of the early innovators on the Solana network.
                    </p>
                </Column>
                <HexadoFutureMap>
                    <Image src={HEXADO_GENERATIVE_DROP} justifyContent="center" />
                    <Column className="hexado-future-info">
                        <CheckPointText>
                            CheckPoint One
                        </CheckPointText>
                        <Row gap="30px">
                            <CheckPoint />
                            <BoxRadientLeft>
                                <Image src={HEXADO_DROPS} />
                            </BoxRadientLeft>
                        </Row>
                        <p>
                            The time to acquire your own unique Hexado NFT from any one of our original, creative drops. Just make sure you don't miss out! This is only the beginning üòà
                        </p>
                    </Column>
                    <Column className="hexado-future-info">
                        <CheckPointText>
                            CheckPoint Two
                        </CheckPointText>
                        <Row gap="30px">
                            <BoxRadientRight>
                                <Image src={HEXADO_VXAIATARS} />
                            </BoxRadientRight>
                            <CheckPoint />
                        </Row>
                        <p>
                            A game focused on having fun. Including addictive, engaging gameplay with complex blockchain integrations. Our incorporated artistic medium is guaranteed to impress. Releasing to holders of the Hexado NFT.
                        </p>
                    </Column>
                    <Image src={HEXAVERSE} />
                    <Image src={HEXADO_STORE} justifyContent="center" />
                    <Column className="hexado-future-info">
                        <CheckPointText>
                            CheckPoint Three
                        </CheckPointText>
                        <Row gap="30px">
                            <CheckPoint />
                            <BoxRadientLeft>
                                <Image src={HEXADO_CLOTHING} />
                            </BoxRadientLeft>
                        </Row>
                        <p>
                            Our first big update, switching from private to public so everyone can experience the intense atmospheres and fast-paced gameplay of the Hexado‚Äôs Project videogame.  But wait‚Ä¶ we're doing a lot more than just going public‚Ä¶.
                        </p>
                    </Column>
                </HexadoFutureMap>
                <Column className="hexado-team" alignItems="center">
                    <Image src={HEXADO_TEAM} />
                    <DevBox className="hexado-leader-card-devbox">
                        <Row className="hexado-leader-card" gap="50px" alignItems="flex-start">
                            <Column>
                                <Image src={HEXADO_MAIN} className="hexado-leader-logo" />
                                <Row className="leader-info" justifyContent="space-between" alignItems="flex-end">
                                    <Image className="hexado-leader-letter" src={HEXADO_MAIN_LETTER} />
                                    <Image className="hexado-leader-role" src={HEXADO_MAIN_ROLE} />
                                </Row>
                            </Column>
                            <p>
                                I imagine some would find it interesting when it comes to knowing a little bit about me as if you are reading this, you are possibly pondering upon the decision of investing in Hexados Project. Sooooo‚Ä¶..

                                I am the creator and orchestrator of Hexados Project. I currently reside in sunny beautiful Southern California. Some of the things I do outside of working on the project are:  surfing, diving, skateboarding, park skiing, and park snowboarding (def gonna use one of those as a form of media for the project).

                                In a way, they are all expressive sports, an outlet similar to art. A way to let off steam and get high on adrenaline at the same time. The only thing is you can't break your arm drawing (can you?).

                                The goal of this project is to make something new, innovative, and interesting. executing on pre-existing ideas and doing stuff that hasn't been done before. Attempting to differ from the regular flaming pile of shit that is the NFT space.
                            </p>
                        </Row>
                    </DevBox>
                    <HexadoDevList>
                        <DevBox>
                            <Column className="hexado-dev-card">
                                <Image src={HEXADO_JV} />
                                <Row className="dev-description" justifyContent="space-between" alignItems="flex-end">
                                    <Image src={HEXADO_JV_LETTER} />
                                    <Image src={HEXADO_JV_ROLE} />
                                </Row>
                                <p>
                                    I'm JV. I made every single level that is featured in the videogame as well as the media/ads/VFX that went along with it.
                                </p>
                            </Column>
                        </DevBox>
                        <DevBox>
                            <Column className="hexado-dev-card">
                                <Image src={HEXADO_KARL} />
                                <Row className="dev-description" justifyContent="space-between" alignItems="flex-end">
                                    <Image src={HEXADO_KARL_LETTER} />
                                    <Image src={HEXADO_KARL_ROLE} />
                                </Row>
                                <p>
                                    Hello I am Karl. I am from Australia and I am doing the entire backend/frontend/server setup and UI of the entire videogame.
                                </p>
                            </Column>
                        </DevBox>
                        <DevBox>
                            <Column className="hexado-dev-card">
                                <Image src={HEXADO_NGU} />
                                <Row justifyContent="space-between" className="dev-description" alignItems="flex-end">
                                    <Image src={HEXADO_NGU_LETTER} />
                                    <Image src={HEXADO_NGU_ROLE} />
                                </Row>
                                <p>
                                    I'm JV. I made every single level that is featured in the videogame as well as the media/ads/VFX that went along with it.
                                </p>
                            </Column>
                        </DevBox>
                    </HexadoDevList>
                    <HexadoTeamList>
                        <Column className="hexado-team-member">
                            <Image src={HEXADO_AIDEN} />
                            <Image src={HEXADO_AIDEN_LETTER} className="member-letter" />
                            <Image src={HEXADO_AIDEN_ROLE} className="member-role" />
                        </Column>
                        <Column className="hexado-team-member">
                            <Image src={HEXADO_COLLIN} />
                            <Image src={HEXADO_COLIN_LETTER} className="member-letter" />
                            <Image src={HEXADO_COLIN_ROLE} className="member-role" />
                        </Column>
                        <Column className="hexado-team-member">
                            <Image src={HEXADO_FITZ} />
                            <Image src={HEXADO_FITZ_LETTER} className="member-letter" />
                            <Image src={HEXADO_FITZ_ROLE} className="member-role" />
                        </Column>
                    </HexadoTeamList>
                </Column>
            </Container>
        </Wrapper>
    )
}

const Wrapper = styled.section` 
    background-color: #03031B;
    display: flex;
    flex-direction: row;
    justify-content: center;
    color: white;
    padding: 90px 20px;
    font-family: Open Sans;
    .hexado-leader-card {
        background-color: #101126;
        padding: 20px;
        .leader-info{
            padding-top: 20px;
        }
        .hexado-leader-logo{
            border-radius: 20px;
            max-width: 340px;
        }
        .hexado-leader-letter{
            max-width: 190px;
        }
        p{
            font-size: 20px;
            line-height: 38px;
            max-width: 890px;
            flex: 1;
            margin: 0px;
        }
        @media screen and (max-width:790px) {
            flex-direction: column;
            align-items: center;
            gap: 30px;
            padding-top: 50px;
            p{
                text-align: center;
            }
        }
    }
    .hexado-about {
        max-width: 850px;
        font-size: 20px;
        line-height: 32px;
        font-family: Open Sans;
        @media screen and (max-width:920px) {
            text-align: center;
        }
    }
    .hexado-description{
        font-family: Open Sans;
        text-align: center;
        font-size: 20px;
        line-height: 32px;
    }
    .intro-hexados{
        padding-top: 120px;
    }
    .hexado-hero{
        @media screen and (max-width:920px) {
            flex-direction: column;
            gap: 20px;

            .hexado-nft{
                order: 1;
            }
            .hexado-info{
                order: 2;
                align-items: center;
            }
        }
    }

    .hexado-influence{
        padding: 150px 0px;
    }

    .hexado-project-2{
        padding-top: 150px;
    }

    .hexado-future{
        padding-top: 150px;
        gap: 30px;
        p{
            max-width: 930px;
            text-align: center;
            font-size: 20px;
        }
    }

    .hexado-future-description{
        @media screen and (max-width:620px) {
            flex-direction: column;
        }
    }

    .hexado-future-info{
        p{
            padding-top: 20px;
            max-width: 720px;
            font-size: 20px;
            line-height: 32px;
        }
        &:nth-child(3){
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            p{
                text-align: right;
            }
        }
    }

    .hexados-hero-btn{
        max-width: 150px;
        color: black;
        border-radius: 0px;
    }

    .hexado-team{
        padding-top: 100px;
        position: relative;
        .hexado-leader-card-devbox{
            margin-top: 50px;
            @media screen and (max-width:980px) {
                max-width: 800px;
            }
        }
        .hexado-dev-card{
            background-color: #101126;
            padding: 20px;
            max-width: 370px;
            height: 100%;
            p{
                font-family: Open Sans;
                font-size: 20px;
                line-height: 32px;
            }
            .dev-description{
                padding-top: 20px;
            }
        }
    }
    

`

const HexadoList = styled.div`
    display: grid;
    list-style: none;
    padding-top: 50px;
    grid-gap: 50px;
    grid-template-columns:repeat(4, minmax(0, 1fr));
    @media screen and (max-width:1290px) {
            grid-template-columns:repeat(2, minmax(0, 1fr));
    }
    @media screen and (max-width:620px) {
            grid-template-columns:repeat(1, minmax(0, 1fr));
    }
    .hexado-card-letter{
        max-width: 170px;
        max-height: 80px;
    }
`

const CheckPointText = styled.h3`
    color: #00E66D;
    font-size: 24px;
`

const HexadoFutureMap = styled.div`
    display: grid;
    row-gap: 50px;
    column-gap: 50px;
    grid-template-columns:repeat(2, minmax(0, 1fr));
    @media screen and (max-width:620px) {
        grid-template-columns:repeat(1, minmax(0, 1fr));
    }
`

const HexadoTeamList = styled.div`
    display: grid;
    padding-top: 50px;
    grid-template-columns:repeat(3, minmax(0, 1fr));
    column-gap: 100px;
    row-gap: 50px;
    .hexado-team-member{
        align-items: center;
        max-width: 370px;
        .member-role{
            max-width: 150px;
        }
        .member-letter{
            padding-top: 30px;
            max-width: 200px;
        }
    }
    @media screen and (max-width:980px) {
        column-gap: 50px;
        grid-template-columns:repeat(2, minmax(0, 1fr));
    }
    @media screen and (max-width:375px) {
        column-gap: 30px;
        row-gap: 30px;
        grid-template-columns:repeat(1, minmax(0, 1fr));
    }
`
const HexadoDevList = styled.div`
    display: grid;
    column-gap: 100px;
    grid-template-columns:repeat(3, minmax(0, 1fr));
    padding-top: 50px;
    padding-bottom: 50px;
    @media screen and (max-width:980px) {
        column-gap: 50px;
        row-gap: 50px;
        grid-template-columns:repeat(2, minmax(0, 1fr));
    }
    @media screen and (max-width:620px) {
        grid-template-columns:repeat(1, minmax(0, 1fr));
    }
`